/* Centers the content inside the element with class 'App' */
.App {
  text-align: center;
}

/* Sets the height for the element with class 'App-logo' and disables pointer events */
.App-logo {
  height: 40vmin; /* Height is set relative to the viewport size */
  pointer-events: none; /* Prevents user interaction, such as clicks or hovers */
}

/* Applies animation to the 'App-logo' class only when reduced motion is not preferred */
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    /* Adds a continuous spinning animation that takes 20 seconds per cycle */
    animation: App-logo-spin infinite 20s linear;
  }
}

/* Styles the header area of the application */
.App-header {
  background-color: #282c34; /* Sets a dark background color */
  min-height: 100vh; /* Makes the header take up the full height of the viewport */
  display: flex; /* Uses flexbox for layout */
  flex-direction: column; /* Arranges child elements vertically */
  align-items: center; /* Centers child elements horizontally */
  justify-content: center; /* Centers child elements vertically */
  font-size: calc(10px + 2vmin); /* Dynamically adjusts font size based on viewport size */
  color: white; /* Sets the text color to white */
}

/* Styles links within the application */
.App-link {
  color: #61dafb; /* Sets link color to a light blue */
}

/* Defines a keyframe animation to spin an element */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); /* Starting state: no rotation */
  }
  to {
    transform: rotate(360deg); /* Ending state: full 360-degree rotation */
  }
}
